:root {
    --main-background-color: #0a192f;
    --main-text-color: #ccd6f6;
    --secondary-color: #64ffda;
    --third-color: #8892b0;
    --forth-color: #112240;
    --intro-delay: 1s;
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    font-family: 'Inter', sans-serif;
    background-color: var(--main-background-color);
}

html {
    scroll-behavior: smooth;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

.container {
    padding-left: 15px;
    padding-right: 15px;
    margin-right: auto;
    margin-left: auto;
}

/* Small Screens */
@media (min-width : 767px) {
    .container {
        width: 750px;
    }
}

/* Medium Screens */
@media (min-width : 992px) {
    .container {
        width: 970px;
    }
}

/* Large Screens */
@media (min-width : 1200px) {
    .container {
        width: 1170px;
    }
}

@media (min-width : 1300px) {
    .container {
        width: 1270px
    }
}

@media (min-width : 1400px) {
    .container {
        width: 1370px;
    }
}

/* Start Preloader */
.preloader {
    background-color: var(--main-background-color);
    overflow: hidden;
    height: 100vh;
    width: 100%;
    position: absolute;
    z-index: 100;
}

.fade {
    /* z-index: -100;
    display: none; */
    opacity: 0;
    visibility: hidden;
}

.preloader h1 {
    position: absolute;
    font-family: "Open Sans";
    font-weight: 600;
    font-size: 12px;
    text-transform: uppercase;
    left: 50%;
    top: 58%;
    margin-left: -20px;
}

.preloader .body {
    position: absolute;
    top: 50%;
    margin-left: -50px;
    left: 50%;
    animation: speeder 0.4s linear infinite;
}

.preloader .body>span {
    height: 5px;
    width: 35px;
    background: #000;
    position: absolute;
    top: -19px;
    left: 60px;
    border-radius: 2px 10px 1px 0;
}

.preloader .base span {
    position: absolute;
    width: 0;
    height: 0;
    border-top: 6px solid transparent;
    border-right: 100px solid #000;
    border-bottom: 6px solid transparent;
}

.preloader .base span::before {
    content: "";
    height: 22px;
    width: 22px;
    border-radius: 50%;
    background: #000;
    position: absolute;
    right: -110px;
    top: -16px;
}

.preloader .base span::after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-top: 0 solid transparent;
    border-right: 55px solid #000;
    border-bottom: 16px solid transparent;
    top: -16px;
    right: -98px;
}

.preloader .face {
    position: absolute;
    height: 12px;
    width: 20px;
    background: #000;
    border-radius: 20px 20px 0 0;
    transform: rotate(-40deg);
    right: -125px;
    top: -15px;
}

.preloader .face::after {
    content: "";
    height: 12px;
    width: 12px;
    background: #000;
    right: 4px;
    top: 7px;
    position: absolute;
    transform: rotate(40deg);
    transform-origin: 50% 50%;
    border-radius: 0 0 0 2px;
}

.preloader .body>span>span:nth-child(1),
.preloader .body>span>span:nth-child(2),
.preloader .body>span>span:nth-child(3),
.preloader .body>span>span:nth-child(4) {
    width: 30px;
    height: 1px;
    background: #000;
    position: absolute;
    animation: fazer1 0.2s linear infinite;
}

.preloader .body>span>span:nth-child(2) {
    top: 3px;
    animation: fazer2 0.4s linear infinite;
}

.preloader .body>span>span:nth-child(3) {
    top: 1px;
    animation: fazer3 0.4s linear infinite;
    animation-delay: -1s;
}

.preloader .body>span>span:nth-child(4) {
    top: 4px;
    animation: fazer4 1s linear infinite;
    animation-delay: -1s;
}

@keyframes fazer1 {
    0% {
        left: 0;
    }

    100% {
        left: -80px;
        opacity: 0;
    }
}

@keyframes fazer2 {
    0% {
        left: 0;
    }

    100% {
        left: -100px;
        opacity: 0;
    }
}

@keyframes fazer3 {
    0% {
        left: 0;
    }

    100% {
        left: -50px;
        opacity: 0;
    }
}

@keyframes fazer4 {
    0% {
        left: 0;
    }

    100% {
        left: -150px;
        opacity: 0;
    }
}

@keyframes speeder {
    0% {
        transform: translate(2px, 1px) rotate(0deg);
    }

    10% {
        transform: translate(-1px, -3px) rotate(-1deg);
    }

    20% {
        transform: translate(-2px, 0px) rotate(1deg);
    }

    30% {
        transform: translate(1px, 2px) rotate(0deg);
    }

    40% {
        transform: translate(1px, -1px) rotate(1deg);
    }

    50% {
        transform: translate(-1px, 3px) rotate(-1deg);
    }

    60% {
        transform: translate(-1px, 1px) rotate(0deg);
    }

    70% {
        transform: translate(3px, 1px) rotate(-1deg);
    }

    80% {
        transform: translate(-2px, -1px) rotate(1deg);
    }

    90% {
        transform: translate(2px, 1px) rotate(0deg);
    }

    100% {
        transform: translate(1px, -2px) rotate(-1deg);
    }
}

.preloader .longfazers {
    position: absolute;
    width: 100%;
    height: 100%;
}

.preloader .longfazers span {
    position: absolute;
    height: 2px;
    width: 20%;
    background: #000;
}

.preloader .longfazers span:nth-child(1) {
    top: 20%;
    animation: lf 0.6s linear infinite;
    animation-delay: -5s;
}

.preloader .longfazers span:nth-child(2) {
    top: 40%;
    animation: lf2 0.8s linear infinite;
    animation-delay: -1s;
}

.preloader .longfazers span:nth-child(3) {
    top: 60%;
    animation: lf3 0.6s linear infinite;
}

.preloader .longfazers span:nth-child(4) {
    top: 80%;
    animation: lf4 0.5s linear infinite;
    animation-delay: -3s;
}

@keyframes lf {
    0% {
        left: 200%;
    }

    100% {
        left: -200%;
        opacity: 0;
    }
}

@keyframes lf2 {
    0% {
        left: 200%;
    }

    100% {
        left: -200%;
        opacity: 0;
    }
}

@keyframes lf3 {
    0% {
        left: 200%;
    }

    100% {
        left: -100%;
        opacity: 0;
    }
}

@keyframes lf4 {
    0% {
        left: 200%;
    }

    100% {
        left: -100%;
        opacity: 0;
    }
}

/* End Preloader */
/* Start Nav Bar */
.navigation .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 0;
    position: relative;
}

.navigation .container .logo {
    font-weight: bold;
    font-size: 1.3rem;
    color: var(--secondary-color);
    cursor: pointer;
}

@media (max-width : 767px) {
    .navigation .container .logo {
        padding-left: 1rem;
    }

    .navigation .container .hamburger-react {
        padding-right: 1rem;
    }
}

.navigation .container .nav-box {
    justify-content: center;
    align-items: center;
    display: flex;
}

.navigation .container .nav-box .side-box {
    display: flex;
    gap: 3.2rem;
}

@media (max-width : 767px) {
    .navigation .container .nav-box {
        display: none;
        position: absolute;
        flex-direction: column;
        right: 0;
        background-color: #112240;
        width: 70%;
        top: 0;
        min-height: 100vh;
        z-index: 100;
    }

    .navigation .container .show {
        display: flex;
    }

    .navigation .container .nav-box .side-box {
        flex-direction: column;
        gap: 5rem;
        align-items: center;
    }

    .navigation .container .nav-box .link {
        font-size: 1.2rem !important;
    }
}

.navigation .container .nav-box .link {
    color: var(--main-text-color);
    font-size: 0.9rem;
}

.navigation .container .nav-box .link:hover {
    color: var(--secondary-color);
}

.navigation .container .hamburger-react {
    color: var(--secondary-color)
}

@media (min-width : 767px) {
    .navigation .container .hamburger-react {
        display: none;
    }
}

@media (max-width : 767px) {
    .navigation .container .hamburger-react {
        z-index: 100;
    }
}


/* End Nav Bar */
/* Start Introduction */
.intro {
    padding: 90px 0;
}

.intro .container {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
}

.intro .container .content {
    width: 80%;
    margin: auto;
}

.intro .container .content .greet-text {
    color: var(--secondary-color);
    margin: 0 0 1.8rem 0.2rem;
    word-spacing: 10px;
}

.intro .container .content .greet-text.animate_fadeInLeft {
    animation-delay: var(--intro-delay);
}

.intro .container .content .name {
    font-size: 5rem;
    color: var(--main-text-color);
}

.intro .container .content .name.animate_fadeInUp {
    animation-delay: var(--intro-delay);
}

.intro .container .content .doing {
    font-size: 5rem;
    margin: 0.6rem 0 0;
    color: var(--third-color);
}

@media (max-width : 767px) {
    .intro .container .content {
        text-align: center;
    }

    .intro .container .content .name,
    .intro .container .content .doing {
        font-size: 2.5rem;
    }
}

.intro .container .content .basic-info {
    color: var(--third-color);
    margin: 1.3rem 0 0;
    line-height: 1.8;
}

.intro .container .content .basice-info.animate_fadeInUpBig,
.intro .container .content .doing.animate_fadeInUpBig {
    animation-delay: var(--intro-delay);
}

.intro .container .content .basic-info span {
    color: var(--secondary-color);
}

/* End Introduction */
/* Start Fixed Contact */
.contact-fixed .container {
    position: relative;
}

@media (max-width : 767px) {
    .contact-fixed {
        display: none;
    }
}

.contact-fixed .container .social-media,
.contact-fixed .container .email {
    position: fixed;
}

.contact-fixed .container .social-media {
    left: 5rem;
    bottom: 6rem;
}

.contact-fixed .container .email {
    right: 0;
    bottom: 12.2rem;
    transform: rotate(90deg);
    color: var(--third-color);
    cursor: pointer;
}

.contact-fixed .container .email:hover {
    color: var(--secondary-color);
}

.contact-fixed .container .email::after {
    content: "";
    position: absolute;
    bottom: 50%;
    width: 5rem;
    height: 2px;
    background-color: var(--third-color);
    right: -7rem;
}

.contact-fixed .container .social-media {
    display: flex;
    gap: 20px;
    flex-direction: column;
}

.contact-fixed .container .social-media::after {
    content: "";
    position: absolute;
    bottom: -7rem;
    width: 2px;
    height: 5rem;
    background-color: var(--third-color);
    left: calc(50% - 1px);
}

.contact-fixed .container .social-media .icon i {
    font-size: 1.5rem;
}

.contact-fixed .container .social-media .icon {
    color: var(--third-color);
}

.contact-fixed .container .social-media .icon i:hover {
    color: var(--secondary-color);
    transform: translateY(-5px);
    transition-duration: 0.2s;
}

/* End Fixed Contact */
/* Start About Section */
.about {
    padding: 7rem 0;
}

.about .container {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
}

.about .container .content {
    width: 80%;
    margin: auto;
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 2rem;
}

@media (max-width : 992px) {
    .about .container .content {
        grid-template-columns: 1fr;
    }

}

@media (max-width : 767px) {
    .about .container .content .info .about-header {
        font-size: 1.5rem;
    }

    .about .container .content .info .my-text {
        font-size: 1rem;
    }

    .about .container .content .info .skills .item {
        font-size: 1rem;
    }
}

@media (max-width : 280xp) {
    .about .container .content .info .about-header {
        font-size: 1rem;
    }

    .about .container .content .info .my-text {
        font-size: 0.8rem;
    }

    .about .container .content .info .skills .item {
        font-size: 0.8rem;
    }
}

.about .container .info {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.about .content .info .about-header {
    color: var(--main-text-color);
    font-size: 2rem;
    position: relative;
}

.about .content .info .about-header span {
    color: var(--secondary-color);
    padding-right: 1rem;
    font-size: 1.3rem;
}

.about .container .content .info .about-header::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 15rem;
    background-color: var(--secondary-color);
    top: 50%;
    left: 40%;
}

@media (max-width : 1200px) {
    .about .container .content .info .about-header::after {
        width: 12rem;
        left: 55%;
    }
}

@media (max-width : 767px) {
    .about .container .content .info .about-header::after {
        width: 7rem;
        left: 70%;
    }
}

@media (max-width : 280px) {
    .about .container .content .info .about-header::after {
        width: 3rem;
        left: 90%;
    }
}

.about .content .info .my-text {
    color: var(--third-color);
    line-height: 1.8;
    font-size: 1.08rem;
}

.about .content .info .my-text span {
    color: var(--secondary-color);
}

.about .content .info .skills {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 0.5rem;
}

.about .content .info .skills .item {
    color: var(--third-color);
    display: flex;
    align-items: center;
}

.about .content .info .skills i {
    color: var(--secondary-color);
    font-size: 0.4rem;
    padding-right: 0.6rem;
    padding-top: 2px;
}

.about .container .personal-image {
    display: flex;
    align-items: center;
}

@media (max-width : 767px) {
    .about .container .personal-image .image-container {
        width: 15rem;
        height: 15rem;
    }

    .about .container .personal-image .image-container img {
        width: 15rem;
        height: 15rem;
    }

    .about .container .personal-image .image-container .outline-box {
        width: 15rem;
        height: 15rem;
    }
}

@media (max-width : 280px) {
    .about .container .personal-image .image-container {
        width: 10rem;
        height: 10rem;
    }

    .about .container .personal-image .image-container img {
        width: 10rem;
        height: 10rem;
    }

    .about .container .personal-image .image-container .outline-box {
        width: 10rem;
        height: 10rem;
    }
}

.about .container .personal-image .image-container {
    position: relative;
    cursor: pointer;
    border-radius: 0.5rem;
}

.about .container .personal-image .image-container::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(100 255 218 / 60%);
    transition: background-color 1s;
    border-radius: 0.5rem;
}

.about .container .personal-image .image-container:hover::before {
    background-color: rgb(100 255 218 / 0%);
}

.about .container .personal-image img {
    width: 20rem;
    height: 20rem;
    border-radius: 0.5rem;
}

.about .container .personal-image .outline-box {
    border: 1px solid var(--secondary-color);
    width: 20rem;
    height: 20rem;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(2rem, 2rem);
    transition: transform 1s;
    z-index: -1;
    border-radius: 0.5rem;
}

.about .container .personal-image .image-container:hover .outline-box {
    transform: translate(1rem, 1rem);
}

/* End About Section */
/* Start Projects Section */
