    color: var(--main-text-color);
    position: relative;
}

.projects .content .projects-header::after {
    content: "";
    position: absolute;
    height: 1px;
    width: 15rem;
    background-color: var(--secondary-color);
    top: 50%;
    left: 50%;
}

@media (max-width : 1200px) {
    .projects .container .content .projects-header::after {
        left: 60%;
    }
}

@media (max-width : 992px) {
    .projects .container .content .projects-header::after {
        width: 10rem;
        left: 70%;
    }
}

@media (max-width : 767px) {
