        gap: 0.5rem;
    }
}

.projects .content .project .info {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.projects .content .project .info .static-text {
    color: var(--secondary-color);
    font-size: 1rem;
    word-spacing: 0.1rem;
    margin-bottom: 0.6rem;
}

.projects .content .project .info .pro-name {
    color: var(--main-text-color);
    font-size: 1.8rem;
    cursor: pointer;
    margin-bottom: 1rem;
}

.projects .content .project .info .pro-name:hover {
    color: var(--secondary-color);
}

.projects .content .info .pro-text {
    background-color: var(--forth-color);
    color: var(--third-color);
    padding: 1.5rem;
    line-height: 1.8;
    position: relative;
    z-index: 2;
    width: 120%;
    border-radius: 0.5rem;
    text-align: end;
    box-shadow: 0 10px 30px -15px #020c1bb3
}

.projects .content .info .skills-used {
    display: flex;
    gap: 2rem;
    margin-top: 2rem;
    color: var(--third-color);
}

.projects .content .info .skills-used .skill {
    cursor: pointer;
}

.projects .content .info .skills-used .skill:hover {
    color: var(--secondary-color);
}

.projects .content .info .anchors {
    display: flex;
    gap: 2rem;
    margin-top: 1rem;
}

.projects .content .info .anchors i {
    font-size: 1.4rem;
    color: var(--third-color);
}

.projects .content .info .anchors i:hover {
    color: var(--secondary-color);
}

/* End Projects Section */
/* Start NoteWorthy Section */
.small-projects {
    padding: 7rem 0;
}

.small-projects .container {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
}

.small-projects .container .content {
    width: 80%;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
    justify-content: center;
    align-items: center;
}

.small-projects .content .section-name {
    text-align: center;
    font-size: 2rem;
    color: var(--main-text-color);
}

.small-projects .content .sub-info {
    color: var(--third-color);
    text-align: center;
    margin-bottom: 2rem;
}

.small-projects .content .sub-info:hover {
    color: var(--secondary-color);
}

.small-projects .content .btn-more {
    width: fit-content;
    padding: 1.3rem 1.9rem;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    border: 1px solid var(--secondary-color);
    background-color: transparent;
    color: var(--secondary-color);
    border-radius: 0.3rem;
    word-spacing: 2px;
    transition: background-color 1s;
    cursor: pointer;
    margin-top: 3rem;
}

.small-projects .content .btn-more:hover {
    background-color: rgb(100 255 218 / 20%);
}

.small-projects .content .mini-cont {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 1.3rem;
}

.small-projects .content .mini-cont .mini {
    display: flex;
    flex-direction: column;
    background-color: var(--forth-color);
    padding: 2rem 1.5rem;
    cursor: pointer;
    box-shadow: 0 10px 30px -15px #020c1bb3;
    border-radius: 0.5rem;
    transition: transform 0.7s;
}

@media (max-width :280px) {
    .small-projects .container .content .mini {
        padding: 1rem;
        width: 250px;
        margin: auto;
    }

    .small-projects .container .content .mini .file-icons>i {
        font-size: 1rem;
    }

    .small-projects .container .content .file-icons a i {
        font-size: 1rem;
    }

    .small-projects .container .content .mini .mini-name {
        margin: 1rem 0 1rem;
    }
}

.small-projects .content .mini-cont .mini:hover {
    transform: translateY(-1rem);
}

.small-projects .content .mini .file-icons {
    display: flex;
    justify-content: space-between;
}

.small-projects .content .mini .file-icons>i {
    font-size: 2rem;
    color: var(--secondary-color);
}

.small-projects .content .file-icons a i {
    color: var(--third-color);
    font-size: 1.3rem;
}

.small-projects .content .file-icons a i:hover {
    color: var(--secondary-color);
}

.small-projects .content .mini-cont .mini .mini-skills {
    display: flex;
    gap: 1rem;
    margin-top: 1.5rem;
}

.small-projects .content .mini .mini-name {
    color: var(--main-text-color);
    margin: 1.3rem 0 1rem;
}

.small-projects .content .mini:hover .mini-name {
    color: var(--secondary-color);
}

.small-projects .content .mini .mini-text {
    color: var(--third-color);
}

.small-projects .content .mini .mini-skills .mini-skill {
    color: var(--third-color);
    font-size: 0.8rem;
}

/* End NoteWorthy Section */
/* Start Mail Section */
.mail {
    padding: 7rem 0 2rem;
}

.mail .container {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
}

.mail .container .content {
    display: grid;
    width: 80%;
    margin: auto;
    grid-template-columns: 1fr;
    justify-content: center;
}

.mail .content .sub-header {
    text-align: center;
    color: var(--secondary-color);
    word-spacing: 5px;
}

.mail .content .header {
    color: var(--main-text-color);
    font-size: 3.8rem;
    text-align: center;
    margin-top: 1.3rem;
    margin-bottom: 1.5rem;
}

.mail .content .info {
    text-align: center;
    max-width: 60%;
    margin: auto;
    font-size: 1.1rem;
    color: var(--third-color);
    line-height: 1.7;
}

.mail .content .my-link {
    width: fit-content;
    padding: 1.3rem 1.9rem;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    border: 1px solid var(--secondary-color);
    background-color: transparent;
    color: var(--secondary-color);
    border-radius: 0.3rem;
    word-spacing: 2px;
    transition: background-color 1s;
    margin-top: 3rem;
    margin-bottom: 12rem;
}

.mail .content .my-link:hover {
    background-color: rgb(100 255 218 / 20%);
}

.mail .content .contact-small {
    margin-bottom: 1.5rem;
    display: none;
    gap: 2rem;
    justify-content: center;
}

@media (max-width :767px) {
    .mail .content .contact-small {
        display: flex;
    }
}

.mail .content .contact-small a {
    font-size: 1.3rem;
    color: var(--third-color);
}

.mail .content .contact-small a:hover {
    color: var(--secondary-color);
}

.mail .content .me {
    text-align: center;
    color: var(--third-color);
    cursor: pointer;
}

.mail .content .me:hover {
    color: var(--secondary-color);
}

/* End Mail Section */